import React from "react";
import {Link, useLocation} from "react-router-dom";

import Logo from "../../assets/beyondLogo.png";
import Cross from "../../assets/cross.svg";
import "./navigationPage.css";

export default function NavigationPage(props) {
  const location = useLocation();
  return (
    <div className="navigation-page">
      <button
        onClick={() => {
          props.showNavigation(false);
        }}
        className="navigation-page__close-btn"
      >
        <img className="navigation-page__close-img" src={Cross} alt="quit" />
      </button>
      <img className="navigation-page__back-img" src={Logo} />
      <div className="navigation-page__links">
        <Link
          to="/"
          style={
            location.pathname !== "/"
              ? {textDecoration: "none"}
              : {textDecoration: "underline", color: "white"}
          }
        >
          <h1>HOME</h1>
        </Link>
        <Link
          to="/whatwedo"
          style={
            location.pathname !== "/whatwedo"
              ? {textDecoration: "none"}
              : {textDecoration: "underline", color: "white"}
          }
        >
          <h1>WHAT WE DO</h1>
        </Link>
        <Link
          to="/team"
          style={
            location.pathname !== "/team"
              ? {textDecoration: "none"}
              : {textDecoration: "underline", color: "white"}
          }
        >
          <h1>TEAM</h1>
        </Link>
        <Link
          to="/work"
          style={
            location.pathname !== "/work"
              ? {textDecoration: "none"}
              : {textDecoration: "underline", color: "white"}
          }
        >
          <h1>WORK</h1>
        </Link>
      </div>
    </div>
  );
}
