import React, { useState } from "react";
import { useMediaQuery } from "react-responsive";

import NavigationPage from "../common/navigationPage";

import Title from "../../assets/Work Landing Title-1.svg";
import Project from "../../assets/Project2.svg";
import ProjectTitle from "../../assets/Project2 Title.svg";
import ProjectDesc from "../../assets/Project2 Desc.svg";
// import './OurWork.css'

import Kimaye from "../../assets/kimaye.png";
import Ellowhale from "../../assets/ellowhale.jpg";
import Ellowhale2 from "../../assets/ellowhale2.jpg";
import Yugma from "../../assets/yugma.png";

import styles from "./OurWork.module.css";
import Header from "../common/header";

export default function OurWork() {
  const [navigation, showNavigation] = useState(false); // for displaying the navigation page
  const isMobileDevice = useMediaQuery({ maxDeviceWidth: 800 });
  return (
    <div>
      {navigation && <NavigationPage showNavigation={showNavigation} />}
      {!navigation && (
        <div>
          <Header showNavigation={showNavigation} />
          <h1
            style={{
              color: "white",
              width: "100vw",
              textAlign: "center",
              fontSize: "3.5em",
              fontFamily: "Prompt",
              fontWeight: "200",
              position: "relative",
            }}
          >
            Our work
          </h1>
          <div className={styles.container}>
            {!isMobileDevice && (
              <div className={styles.bottomPart}>
                <div className={styles.bottomLeft}>
                  <div className={styles.projectContainer}>
                    <img className={styles.projectImg} src={Kimaye} />
                    <p className={styles.projectTitle}>Kimaye</p>
                    <p className={styles.projectDesc}>
                      Bespoke creatives launching 5 brand new fruits on the
                      Kimaye platform.
                    </p>
                  </div>
                  <div className={styles.projectContainer}>
                    <img className={styles.projectImg} src={Ellowhale} />
                    <p className={styles.projectTitle}>Ellowhale</p>
                    <p className={styles.projectDesc}>
                      Custom-made learning management website and mobile app
                      designed and developed to aid in student-learning process.
                    </p>
                  </div>
                </div>
                <div className={styles.bottomRight}>
                  <div className={styles.projectContainer}>
                    <img className={styles.projectImg} src={Ellowhale2} />
                    <p className={styles.projectTitle}>Ellowhale</p>
                    <p className={styles.projectDesc}>
                      Custom-made learning management website and mobile app
                      designed and developed to aid in student-learning process.
                    </p>
                  </div>
                  <div className={styles.projectContainer}>
                    <img className={styles.projectImg} src={Yugma} />
                    <p className={styles.projectTitle}>Yugma</p>
                    <p className={styles.projectDesc}>
                      Tailor-made website and creatives for an active grassroot
                      environmental initiative in India.
                    </p>
                  </div>
                </div>
              </div>
            )}
            {isMobileDevice && (
              <div className={styles.bottomPartMob}>
                <div className={styles.projectContainer}>
                  <img className={styles.projectImg} src={Kimaye} />
                  <p className={styles.projectTitle}>Kimaye</p>
                  <p className={styles.projectDesc}>
                    Bespoke creatives launching 5 brand new fruits on the Kimaye
                    platform.
                  </p>
                </div>
                <div className={styles.projectContainer}>
                  <img className={styles.projectImg} src={Ellowhale} />
                  <p className={styles.projectTitle}>Ellowhale</p>
                  <p className={styles.projectDesc}>
                    Custom-made learning management website and mobile app
                    designed and developed to aid in student-learning process.
                  </p>
                </div>
                <div className={styles.projectContainer}>
                  <img className={styles.projectImg} src={Ellowhale2} />
                  <p className={styles.projectTitle}>Ellowhale</p>
                  <p className={styles.projectDesc}>
                    Custom-made learning management website and mobile app
                    designed and developed to aid in student-learning process.
                  </p>
                </div>
                <div className={styles.projectContainer}>
                  <img className={styles.projectImg} src={Yugma} />
                  <p className={styles.projectTitle}>Yugma</p>
                  <p className={styles.projectDesc}>
                    Tailor-made website and creatives for an active grassroot
                    environmental initiative in India.
                  </p>
                </div>
              </div>
            )}
          </div>
        </div>
      )}
    </div>
  );
}
