import React from "react";
import { useMediaQuery } from "react-responsive";
import Email from "../../assets/Email.svg";
import FooterTabs from "../../assets/FooterTabs.svg";
import Logo from "../../assets/beyondLogo.png";
import Address from "../../assets/Address.svg";
import SocialMedia from "../../assets/Social Media.svg";
import BottomText from "../../assets/BottomText.svg";
import Divider from "../../assets/Divider.svg";
import "./Footer.css";

export default function Footer(props) {
  const isMobileDevice = useMediaQuery({ maxDeviceWidth: 1130 });
  return (
    <div id="footer" className="parent-container section">
      {!isMobileDevice && (
        <div>
          <div
            className="empty-container"
            onClick={() => props.showFooter(false)}
          ></div>
          <div className="footer-container">
            <div className="top-side">
              <div className="left-side">
                <div className="email">
                  <h1 className="lets-talk">Let's talk</h1>
                  <p className="email-id">beyonddesignstudio@gmail.com</p>
                </div>
                <div className="tabs">
                  <p className="tab">Home</p>
                  <p className="tab">What we do</p>
                  <p className="tab">Work</p>
                  <p className="tab">Our Team</p>
                  <p className="tab">Contact us</p>
                </div>
              </div>
              <img className="divider" src={Divider} />
              <div className="right-side">
                <img className="logo" src={Logo} />
                <div className="address">
                  <p className="address-line">
                    D 96, Sudarshan Park, Delhi, NCR. -100122.
                  </p>
                </div>
                <img className="socials" src={SocialMedia} />
              </div>
            </div>
            <div className="bottom-side">
              <p className="footnote">
                All rights reserved. Beyond Design Studio ©2021
              </p>
            </div>
          </div>
        </div>
      )}
      {isMobileDevice && (
        <div>
          <div
            className="empty-container"
            onClick={() => props.showFooter(false)}
          ></div>
          <div className="footer-container">
            <div className="top-side-mob">
              <div className="contact-mob">
                <h1 className="lets-talk-mob">Let's talk</h1>
                <p className="email-id-mob">beyonddesignstudio@gmail.com</p>
              </div>
              <div className="tabs-mob">
                <p className="tab-mob">Home</p>
                <p className="tab-mob">What we do</p>
                <p className="tab-mob">Work</p>
                <p className="tab-mob">Our Team</p>
                <p className="tab-mob">Contact us</p>
              </div>
              <div className="mid-container-mob">
                <div className="mid-left-mob">
                  <div className="address-mob">
                    <p className="address-line-mob">
                      D 96, Sudarshan Park, Delhi, NCR. -100122.
                    </p>
                  </div>
                  <img className="social-mob" src={SocialMedia} />
                </div>
                <div className="mid-right-mob">
                  <img className="beyond-logo-mob" src={Logo} />
                </div>
              </div>
            </div>
            <div className="bottom-side-mob">
              <p className="footnote-mob">
                All rights reserved. Beyond Design Studio ©2021
              </p>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
