import React, { useState } from "react";

import NavigationPage from "../common/navigationPage";
import Header from "../common/header";
import TeamSVG from "../../assets/team.svg";
import "./team.css";

function Leadership() {
  const leaders = [
    "Swayam Saraogi",
    "Ritul Satish",
    "Aarav Varshney",
    "Spaatikka Vedmurthy",
  ];

  const leaderDescriptions = {
    0: ["Founding Partner", "Chief Creative Officer"],
    1: ["Founding Partner", "Chief Experience Officer"],
    2: ["Founding Partner", "Chief Technology Officer"],
    3: ["Partner", "Chief Operating Officer"],
  };

  const Leaders = leaders.map((name, index) => {
    return (
      <div className="member-box">
        <img src={"/imgs/" + name.split(" ")[0] + ".png"} />
        <h2>{name}</h2>
        <h4>{leaderDescriptions[index][0]}</h4>
        <h4>{leaderDescriptions[index][1]}</h4>
      </div>
    );
  });

  return (
    <div>
      <h1 className="team-header">Leadership</h1>
      <div className="members-box">{Leaders}</div>
    </div>
  );
}

