function AdminTeam() {
  const members = ["Vernika Mrig", "Ishani Chanda"];
  const descriptions = ["Operations", "Logistics"];
  const Admins = members.map((name, index) => {
    return (
      <div className="member-box">
        <img src={"/imgs/" + name.split(" ")[0] + ".png"} />
        <h2>{name}</h2>
        <h3>{descriptions[index]}</h3>
      </div>
    );
  });

  return (
    <div>
      <h1 className="team-header">Admin Team</h1>
      <div className="members-box">{Admins}</div>
    </div>
  );
}

export default function Team() {
  const [navigation, showNavigation] = useState(false); // for displaying the navigation page

  return (
    <div>
      {navigation && <NavigationPage showNavigation={showNavigation} />}
      {!navigation && (
        <div>
          <Header showNavigation={showNavigation} />
          <div style={{ position: "absolute", marginTop: "15vh" }}>
            <div className="team-container">
              <Leadership />
              <DesignTeam />
              <AdminTeam />
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
