import React from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

// Components:
import LandingPage from "./components/LandingPage/landingPage";
import WhatWeDo from "./components/whatWeDo/whatWeDo";
import Team from "./components/team/team";
import OurClients from "./components/OurClients/OurClients";
import TalkToUs from "./components/TalkToUs/TalkToUs.js";
import Test from "./components/Test/Test.js";
import OurWork from "./components/OurWork/OurWork";

function App() {
  return (
    <div className="App">
      <Router>
        <Switch>
          <Route path="/" exact component={LandingPage} />
          <Route path="/whatwedo" exact component={WhatWeDo} />
          <Route path="/team" exact component={Team} />
          <Route path="/work" exact component={OurWork} />
          <Route path="/our-clients" exact component={OurClients} />
          <Route path="/talk-to-us" exact component={TalkToUs} />
          <Route path="/test" exact component={Test} />
        </Switch>
      </Router>
    </div>
  );
}

export default App;
