import React, { useState, useEffect, useRef } from "react";
import { Link } from "react-router-dom";
import { useIsVisible } from "react-is-visible";
import { useMediaQuery } from "react-responsive";

import NavigationPage from "../common/navigationPage";
import OurClients from "../OurClients/OurClients";
import TalkToUs from "../TalkToUs/TalkToUs";
import PageScroll from "../PageScroll/pagescroll";
import "./landingPage.css";

import Arrow from "../../assets/link-arrow.svg";
import Logo from "../../assets/beyondLogo.png";
import Menu from "../../assets/menu.svg";
import RedSmoke from "../../assets/RedSmoke.mp4";
import KineticTypography from "../../assets/kinetic_typo.mp4";
import KineticTypographyPhone from "../../assets/kinetic_typo_phone.mp4";
import WhatWeDo from "../../assets/whatwedo.svg";
import Team from "../../assets/theteam.svg";
import Work from "../../assets/work.svg";
import Footer from "../Footer/Footer";

function Home(props) {
  const changeToLandingOnUpwardScroll = (e) => {
    if (e.deltaY < 0) {
      props.showHome(false);
    }
  };

  const isMobileDevice = useMediaQuery({ maxDeviceWidth: 1024 });
  const nodeRef = useRef();
  const isVisible = useIsVisible(nodeRef);

  const nodeRef2 = useRef();
  const isVisible2 = useIsVisible(nodeRef2);

  const nodeRef3 = useRef();
  const isVisible3 = useIsVisible(nodeRef3);

  useEffect(() => {
    if (!isMobileDevice) {
      new PageScroll("#pagescroll", {
        animDuration: 500,
        easing: "ease-in",
      });
    }
  }, [isMobileDevice]);

  const showFooter = () => {
    let offsetTop = document.getElementById("footer").offsetTop;
    window.scrollTo({
      top: offsetTop,
      behavior: "smooth",
    });
  };

  return (
    <div className={"home-page"}>
      {!isMobileDevice && (
        <div>
          <img
            src={Logo}
            className="home-page__logo"
            onTouchStart={() => {
              props.showHome(false);
            }}
            onClick={() => {
              props.showHome(false);
            }}
          />
          <div id="pagescroll">
            <div
              onWheel={changeToLandingOnUpwardScroll}
              className="home-page__kinetic-typo section"
            >
              <div className="home-page__kinetic-typo__vid-cont">
                <video
                  className="home-page__kinetic-typo__vid"
                  src={KineticTypography}
                  autoPlay
                  loop
                  muted
                />
                <video
                  className="home-page__kinetic-typo__vid--phone"
                  src={KineticTypographyPhone}
                  autoPlay
                  loop
                  muted
                />
              </div>
            </div>
            <div className="home-page__nav-cont section">
              <img src={WhatWeDo} className="home-page__whatwedo" />
              <div
                ref={nodeRef}
                className={`${isVisible ? "animated-text" : ""}`}
              >
                <h1 className="home-page__nav__heading">What We Do?</h1>
                <p className="home-page__nav__text">
                  We are a one stop solution for all your design needs. Having a
                  dynamic group of UI/UX, development and visual designers
                  working in tandem means that we can take care of your design
                  needs. You needn't look further.
                </p>
                <Link
                  to="/whatwedo#header-start"
                  style={{ textDecoration: "none" }}
                >
                  <img src={Arrow} className="home-page__nav__arrow" />
                </Link>
              </div>
            </div>
            <div className="home-page__nav-cont section">
              <div
                ref={nodeRef3}
                className={`${isVisible3 ? "animated-text" : ""}`}
              >
                <h1 className="home-page__nav__heading">Team</h1>
                <p className="home-page__nav__text">
                  We are a team of young dynamic aspiring designers with a keen
                  eye and an open ear to everything design in the 21st century.
                  Check out our team.
                </p>
                <Link
                  to="/team#header-start"
                  style={{ textDecoration: "none" }}
                >
                  <img src={Arrow} className="home-page__nav__arrow" />
                </Link>
              </div>
              <img src={Team} className="home-page__team" />
            </div>
            <div className="home-page__nav-cont section" id="work">
              <img src={Work} className="home-page__work" />
              <div
                ref={nodeRef2}
                className={`${isVisible2 ? "animated-text" : ""}`}
              >
                <h1 className="home-page__nav__heading">Work</h1>
                <p className="home-page__nav__text">
                  Typical of our futuristic outlook, we think of our designs in
                  terms of what the brand hopes to achieve withing the next 10
                  years. Our work reflects your path to success.
                </p>
                <Link
                  to="/work#header-start"
                  style={{ textDecoration: "none" }}
                >
                  <img src={Arrow} className="home-page__nav__arrow" />
                </Link>
              </div>
            </div>
            <OurClients />
            <TalkToUs showFooter={showFooter} />
            <Footer />
          </div>
        </div>
      )}
      {isMobileDevice && (
        <div>
          <div
            onWheel={changeToLandingOnUpwardScroll}
            className="home-page__kinetic-typo section"
          >
            <img
              src={Logo}
              className="home-page__kinetic-typo__logo"
              onTouchStart={() => {
                props.showHome(false);
              }}
            />
            <div className="home-page__kinetic-typo__vid-cont">
              <video
                className="home-page__kinetic-typo__vid"
                src={KineticTypography}
                autoPlay
                loop
                muted
              />
              <video
                className="home-page__kinetic-typo__vid--phone"
                src={KineticTypographyPhone}
                autoPlay
                loop
                muted
              />
            </div>
          </div>
          <div className="home-page__nav-cont section">
            <img src={WhatWeDo} className="home-page__whatwedo" />
            <h1 className="home-page__nav__heading">What We Do?</h1>
            <p className="home-page__nav__text">
              We are a one stop solution for all your design needs. Having a
              dynamic group of UI/UX, development and visual designers working
              in tandem means that we can take care of your design needs. You
              needn't look further.
            </p>
            <Link
              to="/whatwedo#header-start"
              style={{ textDecoration: "none" }}
            >
              <img src={Arrow} className="home-page__nav__arrow" />
            </Link>
          </div>
          <div className="home-page__nav-cont section">
            <h1 className="home-page__nav__heading">Team</h1>
            <p className="home-page__nav__text">
              We are a team of young dynamic aspiring designers with a keen eye
              and an open ear to everything design in the 21st century. Check
              out our team.
            </p>
            <Link to="/team#header-start" style={{ textDecoration: "none" }}>
              <img src={Arrow} className="home-page__nav__arrow" />
            </Link>
            <img src={Team} className="home-page__team" />
          </div>
          <div className="home-page__nav-cont section" id="work">
            <img src={Work} className="home-page__work" />
            <h1 className="home-page__nav__heading">Work</h1>
            <p className="home-page__nav__text">
              Typical of our futuristic outlook, we think of our designs in
              terms of what the brand hopes to achieve withing the next 10
              years. Our work reflects your path to success.
            </p>
            <Link to="/work#header-start" style={{ textDecoration: "none" }}>
              <img src={Arrow} className="home-page__nav__arrow" />
            </Link>
          </div>
          <OurClients />
          <TalkToUs showFooter={showFooter} />
          <Footer />
        </div>
      )}
    </div>
  );
}

export default function LandingPage() {
  const [navigation, showNavigation] = useState(false); // for displaying the navigation page
  const [home, showHome] = useState(false); // for displaying the small view

  const changeToHomeOnDownwardScroll = (e) => {
    if (e.deltaY > 0) {
      showHome(true);
    }
  };

  return (
    <div>
      {navigation && <NavigationPage showNavigation={showNavigation} />}
      {!navigation && (
        <div className="landing-page">
          <img
            onClick={() => {
              showNavigation(true);
            }}
            className="land-home__nav"
            src={Menu}
          />
          {!home ? (
            <div
              className="landing-page__cont"
              onWheel={changeToHomeOnDownwardScroll}
            >
              <div className="landing-page__video-cont">
                <video src={RedSmoke} autoPlay loop muted></video>
              </div>
              <img src={Logo} className="landing-page__logo" />
              <p className="landing-page__text">
                Design the future. <br /> Create with us now.
              </p>
              <div className="landing-page__scroll-btn">
                <div
                  className="landing-page__scroll-btn__arrow"
                  onTouchStart={() => {
                    showHome(true);
                  }}
                >
                  <svg
                    width="16"
                    height="8"
                    fill="#fff"
                    viewBox="0 .09 16 7.821"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <svg
                      width="16"
                      height="8"
                      viewBox="0.498 0.36 15.003 7.333"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        d="M.77.597a1.032 1.032 0 0 1 1.315 0L8 5.729 13.914.597a1.032 1.032 0 0 1 1.315 0 .736.736 0 0 1 0 1.143L8.807 7.312a.84.84 0 0 1-.132.145c-.186.162-.432.24-.675.237a1.005 1.005 0 0 1-.674-.237.838.838 0 0 1-.133-.145L.771 1.74a.756.756 0 0 1-.273-.572C.498.962.59.755.771.597z"
                        fill-rule="evenodd"
                        paint-order="stroke fill markers"
                      />
                    </svg>
                  </svg>
                </div>
              </div>
            </div>
          ) : (
            <Home showHome={showHome} />
          )}
        </div>
      )}
      )
    </div>
  );
}
