import React, { useState } from "react";
import { useMediaQuery } from "react-responsive";

import NavigationPage from "../common/navigationPage";
import Footer from "../../components/Footer/Footer";
import Logo from "../../assets/beyondLogo.png";
import CreateTogether from "../../assets/CreateTogether.svg";
import ContactButton from "../../assets/ContactButton.svg";
// import './TalkToUs.css'
import styles from "./TalkToUs.module.css";
import Header from "../common/Header/Header";

export default function TalkToUs(props) {
  const [footer, showFooter] = useState(false);

  return (
    <div className="section">
      <div>
        <div className={styles.contactContainer}>
          <h1 className={styles.createTogether}>Let's create together</h1>
          <button
            className={styles.contactBtn}
            src={ContactButton}
            onClick={() => props.showFooter()}
          >
            Talk to us{" "}
          </button>
        </div>
      </div>
    </div>
  );
}
