import React from "react";
import { Link } from "react-router-dom";
import { useMediaQuery } from "react-responsive";

import Logo from "../../assets/beyondLogo.png";
import Menu from "../../assets/menu.svg";
import "./header.css";

export default function Header(props) {
  const isMobileDevice = useMediaQuery({ maxDeviceWidth: 1024 });

  if (!isMobileDevice) {
    return (
      <div className="header" id="header-start">
        <Link style={{ width: "inherit", textDecoration: "none" }} to="/">
          <img src={Logo} className="header__logo" />
        </Link>
        <img
          onClick={() => {
            props.showNavigation(true);
          }}
          src={Menu}
          className="header__menu"
        />
      </div>
    );
  } else {
    return (
      <div className="header-1024">
        <img src={Logo} className="header-1024__logo" />
        <img
          onClick={() => {
            props.showNavigation(true);
          }}
          src={Menu}
          className="header-1024__menu"
        />
      </div>
    );
  }
}
