import React, { useRef } from "react";
import { useMediaQuery } from "react-responsive";
import { useIsVisible } from "react-is-visible";

import Yugma from "../../assets/YugmaLogo.svg";
import SG from "../../assets/SGLogo.svg";
import Ellowhale from "../../assets/EllowhaleLogo.svg";

import "./OurClients.css";
import { Carousel } from "react-responsive-carousel";

// import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
export default function OurClients() {
  const isMobileDevice = useMediaQuery({ maxDeviceWidth: 800 });
  const nodeRef = useRef();
  const isVisible = useIsVisible(nodeRef);

  return (
    <div className="section">
      {!isMobileDevice && (
        <div>
          <div className="clients-container">
            <h1 className="title">Our Clientele</h1>
            {/* <p className="title">Our Clientele</p> */}
            <div
              ref={nodeRef}
              className={`img-container ${
                isVisible ? "img-container-anim" : ""
              }`}
            >
              <img src={Yugma} />
              <img src={Ellowhale} />
              <img src={SG} />
            </div>
          </div>
        </div>
      )}

      {isMobileDevice && (
        <div>
          <div className="clients-container-mob">
            <h1 className="title-mob">Our Clientele</h1>
            {/* <p className="title">Our Clientele</p> */}
            <div className="img-container-mob">
              <Carousel
                infiniteLoop
                showThumbs={false}
                showStatus={false}
                showArrows={false}
                width="80%"
                autoPlay={true}
                stopOnHover={true}
                showIndicators={false}
                transitionTime={500}
                interval={3500}
              >
                <div>
                  <img style={{ width: "50%", paddingTop: "5%" }} src={Yugma} />
                </div>
                <div>
                  <img style={{ width: "65%" }} src={Ellowhale} />
                </div>
                <div>
                  <img style={{ width: "60%" }} src={SG} />
                </div>
              </Carousel>
              {/* carousel?????? */}
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
