class PageScroll {
  constructor(selector, opts) {
    this.root = document.querySelector(selector);
    this.sections = this.root.querySelectorAll(".section");
    this.opts = opts;
    this.level = 0;
    this.bound = this.sections.length;
    this.isAnimating = false;
    this.setup();
  }
  setup() {
    this.sections.forEach(
      (elem) =>
        (elem.style.transition = `transform ${+this.opts.animDuration / 1000 + "s" || "0.3s"
          }`)
    );
    this.sections.forEach(
      (elem) =>
        (elem.style.transitionTimingFunction = this.opts.easing
          ? this.opts.easing
          : "linear")
    );
    this.moveup = this.moveup.bind(this);
    this.movedown = this.movedown.bind(this);
    this.root.addEventListener("wheel", (event) => {
      if (event.deltaY > 0) this.movedown();
      else this.moveup();
    });
  }
  moveup() {
    if (this.level !== 0 && !this.isAnimating) {
      this.isAnimating = true;
      this.level--;
      this.sections.forEach(
        (elem) => (elem.style.transform = `translateY(${-this.level * 100}%)`)
      );
      setTimeout(() => {
        this.isAnimating = false;
      }, this.opts.animDuration || 300);
    }
  }
  movedown() {
    if (this.level + 1 < this.bound && !this.isAnimating) {
      this.isAnimating = true;
      this.level++;
      this.sections.forEach(
        (elem) => (elem.style.transform = `translateY(${-this.level * 100}%)`)
      );
      setTimeout(() => {
        this.isAnimating = false;
      }, this.opts.animDuration || 300);
    }
  }
  moveto(pos) {
    if (pos >= 0 && pos <= this.bound) {
      let movingTo = this.level - pos;
      console.log(movingTo * 100);
      this.sections.forEach(
        (elem) =>
          (elem.style.transform = `translateY(${(movingTo - this.level) * 100
            }%)`)
      );
      this.level = pos;
    }
  }
}
export default PageScroll;
