import React, { useState, useEffect } from "react";
import { useMediaQuery } from "react-responsive";

import Cross from "../../assets/cross.svg";
import NavigationPage from "../common/navigationPage";
import Header from "../common/header";

import "./whatWeDo.css";

// services and deparment descriptions will be written to an object, based on props, the description
// will be used in the services component

const servicesDes = [
  {
    desc:
      "We understand that good design corresponds to good business. Our branding strategy focuses on making the brand unique and succinct by making designs that will make it more visible in the world, Our designs will aim to enhance your market positioning and brand identity. We offer the following services -",
    specializations: [
      "Brand Identity",
      "Art Direction",
      "Logo",
      "Typography",
      "Illustration",
      "Iconography",
    ],
  },
  {
    desc:
      "We produce designs that ensure high-quality user experiences. Our designs are attractive and functional and built with the target audience in mind. We conduct thorough research and use our skills and expertise to give you the best user experience possible. We focus on making human centered applications and softwares that are scientifically crafted for the target audience. We offer the following services -",
    specializations: [
      "Interface Design",
      "App Design",
      "User Journey Mapping",
      "Packaging Design",
      "Web Design",
      "Graphic Design",
    ],
  },
  {
    desc:
      "We bring your creative designs to technological reality by coding them appropriately. We focus on - ",
    specializations: [
      "Website Development",
      "",
      "App Development",
      "",
      "Technical Assistance",
      "",
      "CMS Implementation",
    ],
  },
];

const departmentDes = [
  "We look to translate though into reality. We use visuals, typography and creative artistry to solve problems and improve brands. We also create products from scratch allowing for inovation and originality to take flight.",
  "We design user centered interfaces for software and apps that solve real world user problems. We aim to connect business goals to user needs by defining product strategy through research testing and refinement",
  "We bring your fascinating ideas from a scintillating design to a working application. We are a team of web and app developers who can turn your design ideas into a reality",
];

function CreateUnorderedList(props) {
  const list = props.specializations.map((item) => (
    <li
      style={
        item === "" ? { listStyleType: "none" } : { listStyleType: "disc" }
      }
    >
      {item}
    </li>
  ));
  return <ul>{list}</ul>;
}

function Services(props) {
  const [servicesAnimate, changeServicesAnimate] = useState({
    enter: -1,
    exit: -1,
  });

  const onEnter = (serviceNo) => {
    changeServicesAnimate({ enter: serviceNo, exit: -1 });
  };

  const onExit = (serviceNo) => {
    changeServicesAnimate({ enter: -1, exit: serviceNo });
  };

  const getServiceClassName = (serviceNo) => {
    if (servicesAnimate.enter === serviceNo) {
      return "services__left-text--enter-animation text-" + serviceNo;
    } else if (servicesAnimate.exit === serviceNo) {
      return "services__left-text--exit-animation text-" + serviceNo;
    } else {
      return "services__left-text text-" + serviceNo;
    }
  };

  return (
    <div className={props.name.toLowerCase()}>
      <h1 className="services__left-heading">Our {props.name}</h1>
      <div
        onMouseEnter={() => {
          onEnter(0);
        }}
        onMouseLeave={() => {
          onExit(0);
        }}
        className={getServiceClassName(0)}
      >
        <span>01</span>
        <hr />
        <p className="service__left-text">
          {props.name === "Services" ? "Brand Identity" : "Visual Design"}
        </p>
      </div>
      <div
        onMouseEnter={() => {
          onEnter(1);
        }}
        onMouseLeave={() => {
          onExit(1);
        }}
        className={getServiceClassName(1)}
      >
        <span>02</span>
        <hr />
        <p className="service__left-text">
          {props.name === "Services" ? "Digital Experience Design" : "UI/UX"}
        </p>
      </div>
      <div
        onMouseEnter={() => {
          onEnter(2);
        }}
        onMouseLeave={() => {
          onExit(2);
        }}
        className={getServiceClassName(2)}
      >
        <span>03</span>
        <hr />
        <p className="service__left-text">
          {props.name === "Services" ? "Engineering" : "Development"}
        </p>
      </div>

      {servicesAnimate.enter > -1 && (
        <div
          className={
            props.name.toLowerCase() +
            "-text " +
            (props.name === "Departments" || servicesAnimate.enter === 2
              ? "service-pos-" +
                Math.abs(
                  servicesAnimate.enter > -1
                    ? servicesAnimate.enter
                    : servicesAnimate.exit
                )
              : "")
          }
        >
          <p className="service-text-animation">
            {props.name === "Services"
              ? servicesDes[servicesAnimate.enter]["desc"]
              : departmentDes[servicesAnimate.enter]}
          </p>
          {props.name === "Services" && (
            <CreateUnorderedList
              specializations={
                servicesDes[servicesAnimate.enter]["specializations"]
              }
            />
          )}
        </div>
      )}
    </div>
  );
}

function Services1024(props) {
  const [serviceShow, changeServiceShow] = useState(-1);

  return (
    <div>
      <h1 className="what-we-do__heading--1024">Our {props.name}</h1>
      {serviceShow === -1 ? (
        <div
          style={{
            height: "70vh",
            display: "flex",
            flexDirection: "column",
            justifyContent: "space-evenly",
            margin: 0,
          }}
        >
          <div
            className="services-cont--1024"
            onClick={() => {
              changeServiceShow(0);
            }}
          >
            <span>01</span>
            <p>
              {props.name === "Services" ? "Brand Identity" : "Visual Design"}
            </p>
          </div>
          <div
            className="services-cont--1024"
            onClick={() => {
              changeServiceShow(1);
            }}
          >
            <span>02</span>
            <p>
              {props.name === "Services"
                ? "Digital Experience Design"
                : "UI/UX"}
            </p>
          </div>
          <div
            className="services-cont--1024"
            onClick={() => {
              changeServiceShow(2);
            }}
          >
            <span>03</span>
            <p>{props.name === "Services" ? "Engineering" : "Development"}</p>
          </div>
        </div>
      ) : (
        <div className="services-cont__desc--1024">
          <img
            onClick={() => {
              changeServiceShow(-1);
            }}
            src={Cross}
            width="5%"
            style={{ position: "relative", left: "80%" }}
          />
          <p>
            {props.name === "Services"
              ? servicesDes[serviceShow]["desc"]
              : departmentDes[serviceShow]}
          </p>
          {props.name === "Services" && (
            <CreateUnorderedList
              specializations={servicesDes[serviceShow]["specializations"]}
            />
          )}
        </div>
      )}
    </div>
  );
}

export default function WhatWeDo() {
  const [navigation, showNavigation] = useState(false); // for displaying the navigation page

  const isMobileDevice = useMediaQuery({ maxDeviceWidth: 1024 });

  // useEffect(() => {
  // new PageScroll("#pagescroll", {
  // animDuration: 500,
  // easing: "ease-in",
  // });
  // }, [])

  return (
    <div>
      {navigation && <NavigationPage showNavigation={showNavigation} />}
      {!navigation && !isMobileDevice && (
        <div>
          <Header showNavigation={showNavigation} />
          <div className="what-we-do">
            <p className="what-we-do__left">What We Do?</p>
            <p className="what-we-do__right">
              We are a team of young, dynamic and aspiring designers who give
              our 100% to every project we take up. Our work shows not only our
              skills but also the hours of dedication we put into researching
              our designs. We create, evaluate and recreate - we make no
              compromises. Like all great designs in the world, our motto is to
              keep it simple, yet significant.
            </p>
          </div>
          <div style={{ position: "absolute", top: "56vw" }}>
            <Services name={"Services"} />
            <Services name={"Departments"} />
          </div>
        </div>
      )}
      {!navigation && isMobileDevice && (
        <div>
          <Header showNavigation={showNavigation} />
          <div style={{ color: "white", position: "absolute", top: "10vh" }}>
            <h1 className="what-we-do__heading--1024">What we do?</h1>
            <p className="what-we-do__desc--1024">
              We are a team of young, dynamic and aspiring designers who give
              our 100% to every project we take up. Our work shows not only our
              skills but also the hours of dedication we put into researching
              our designs. We create, evaluate and recreate - we make no
              compromises. Like all great designs in the world, our motto is to
              keep it simple, yet significant.
            </p>
            <Services1024 name={"Services"} />
            <Services1024 name={"Departments"} />
          </div>
        </div>
      )}
    </div>
  );
}
